let apiurl = process.env.NODE_ENV ? 'http://localhost:5566/api' : '/api' // use current web page as host

export let getThread = function (thread) {
	return fetch(apiurl + '/get-thread/' + thread)
		.then(r => r.json())
}

export let getSongs = function () {
	return fetch(apiurl + '/get-songs')
		.then(r => r.json())
}

export let sendToRender = function (questionData, commentData, options) {
	return fetch(apiurl + '/render-video', {
		method: 'POST',
		body: JSON.stringify({
			questionData,
			commentData,
			options,
		})
	})
		.then(r => r.json())
}