import React from 'react'
import Comment from './Comment'
import './index.css'
import { estimateCommentHtml, getFull } from '../estimate';

class PickComments extends React.Component {
	constructor(props) {
		super(props)

		// Put the comments into a map for easier indexing later
		let mapping = comment => {
			let newcomment = Object.assign({}, comment)

			if (Array.isArray(newcomment.replies)) {
				newcomment.replies = newcomment.replies.map(mapping)
			}

			return {
				enabled: false,
				data: newcomment
			}
		}

		let settings = props.comments.map(mapping)

		this.state = { current: 0, settings }
		this.onSubmit = this.onSubmit.bind(this)
		this.handle = this.handle.bind(this)
		this.toggleCurrent = this.toggleCurrent.bind(this)
		this.handleKeyEvent = this.handleKeyEvent.bind(this)
	}
	handleKeyEvent(e) {
		switch (e.code) {
			case "KeyA":
			case "ArrowLeft":
				this.goPrev()
				break;
			case "KeyD":
			case "ArrowRight":
				this.goNext()
				break
			case "KeyE":
			case "Space":
				this.toggleCurrent()
				break
			case "Enter":
				this.onSubmit()
				break
			default:
		}
	}
	componentDidMount() {
		window.addEventListener('keydown', this.handleKeyEvent)
	}
	componentWillUnmount() {
		window.removeEventListener('keydown', this.handleKeyEvent)
	}
	goPrev() {
		if (this.state.current > 0) {
			this.setState({ current: this.state.current - 1 })
		}
	}
	goNext() {
		if (this.state.current < this.state.settings.length - 1) {
			this.setState({ current: this.state.current + 1 })
		}
	}
	onSubmit() {
		let recFilter = d => {
			let news = d.filter(h => h.enabled)
				.map(d => d.data)
				.map(d => {
					if (d.replies) {
						d.replies = recFilter(d.replies)
					}
					return d
				})
			return news
		}
		let filt = recFilter(this.state.settings)
		this.props.onSelection(filt)
	}
	handle(id) {
		if (this.state.chosen.has(id)) {
			this.state.chosen.set(id)
		} else {
			this.state.chosen.add(id)
		}
		this.setState({
			chosen: this.state.chosen,
		})
	}
	toggleCurrent() {
		let set = this.state.settings

		let opt = set[this.state.current]
		opt.enabled = !opt.enabled
		set[this.state.current] = opt

		console.log(set[this.state.current])

		this.setState({
			settings: set,
		})
	}
	toMinSec(seconds) {
		let fullMins = Math.floor(seconds / 60) // No. full mins
		let restSecs = ('00' + Math.round(seconds - 60 * fullMins)).slice(-2)
		let mins = ('00' + fullMins).slice(-2)
		return `${mins}:${restSecs}`
	}
	calculateTotalTime() {
		let recAdd = comment => {
			let fullHtml = comment.data.body_html

			if (Array.isArray(comment.data.replies)) {
				fullHtml += comment.data.replies
					.filter(r => r.enabled)
					.map(recAdd)
					.reduce((a, b) => a + b, '')
			}
			
			return fullHtml
		}
		let commentTimes = this.state.settings
			.filter(d => d.enabled)
			.map(recAdd)
			.map(b => estimateCommentHtml(b).estimate)

		return Math.round(1e2 * getFull(commentTimes)) / 1e2
	}
	onCurrentCommentChange(e) {
		// let set = this.state.settings

		// let data = set[this.state.current].data
		// data.body_html = e
		// set[this.state.current].data = data

		// this.setState({ settings: set })
		this.setState({ settings: this.state.settings })
	}
	render() {
		// Settings:
		// Enable
		// Keep edits
		let comment = this.state.settings[this.state.current]
		let selectedComments = this.state.settings.filter(d => d.enabled).length
		let { estimate, words, splits } = estimateCommentHtml(comment.data.body_html)
		return (
			<div className="Comments">
				{
					<div>
						<div className="top-comment">
							<Comment comment={comment} onChange={this.onCurrentCommentChange.bind(this)} />
						</div>
						<div className="options">
							<label>
								<input type="checkbox" checked={this.state.settings[this.state.current].enabled} onChange={this.toggleCurrent.bind(this)} />
								Enable
						</label>
							<div>Estimate: {Math.round(1e2 * estimate) / 1e2} s, {words}/{splits}</div>
						</div>
						<div className="controls">
							<button disabled={this.state.current <= 0} onClick={this.goPrev.bind(this)}>Prev</button>
							<input style={{ width: 60 }} type="number" value={this.state.current + 1} onChange={e => this.setState({ current: Math.max(0, parseInt(e.target.value) - 1 || 0) })} />
							<button disabled={this.state.current > this.state.settings.length} onClick={this.goNext.bind(this)}>Next</button>
						</div>
						<div className="controls">
							<span>Total time: {this.toMinSec(this.calculateTotalTime())}, {selectedComments} {selectedComments === 1 ? 'comment' : 'comments'} selected</span>
							<button onClick={this.onSubmit}>Finish</button>
						</div>
					</div>
				}
			</div>
		)
	}
}

export default PickComments;