import React from 'react'
import { getSongs } from './api';

class Settings extends React.Component {
	constructor(props) {
		super(props)

		this.state = { song: null, songs: null }

		getSongs().then(songs => {
			this.setState({ songs, song: songs[0] })
		})
	}
	onSubmit(e) {
		e.preventDefault()
		this.props.onSelection({
			song: this.state.song,
		})
	}
	changeSong(e) {
		this.setState({
			song: e.target.value,
		})
	}
	render() {
		// Settings:
		// Song
		// TODO: dark/light style
		// TODO: Multiple styles (different channels)

		return (
			<div className="Thread">
				<form onSubmit={this.onSubmit.bind(this)}>
					<div>
						{
							this.state.songs &&
							<select onChange={this.changeSong.bind(this)}>
								{this.state.songs.map(song => <option key={song}>{song}</option>)}
							</select>
						}
					</div>
					<div>
						<button type="submit">Send to the slaughterhouse</button>
					</div>
				</form>
			</div>
		)
	}
}

export default Settings;