import React from 'react'

import ReactQuill from 'react-quill'

class Comment extends React.Component {
	handlePress(e) {
		e.stopPropagation()
	}
	propagateHtmlChange(val) {
		this.props.comment.data.body_html = val
		this.props.onChange()
	}
	propagateEnableChange() {
		this.props.comment.enabled = !this.props.comment.enabled
		this.props.onChange()
	}
	render() {
		let { comment, onChange } = this.props
		return (
			<div className="Comment">
				<input className="left-box" type="checkbox" checked={comment.enabled} onChange={this.propagateEnableChange.bind(this)} />
				<div>
					<div onClick={this.propagateEnableChange.bind(this)}>
						<span>{comment.data.score > 1e3 ? Math.round(comment.data.score / 1e2) / 1e1 + 'k' : comment.data.score} points</span>
						<span className="gilding">{comment.data.gildings.gid_3 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-platinum-cartoon.png" alt="platinum" width={16} />
						}{comment.data.gildings.gid_3 > 1 && comment.data.gildings.gid_3}</span>
						<span className="gilding">{comment.data.gildings.gid_2 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-gold-cartoon.png" alt="gold" width={16} />
						}{comment.data.gildings.gid_2 > 1 && comment.data.gildings.gid_2}</span>
						<span className="gilding">{comment.data.gildings.gid_1 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-silver-cartoon.png" alt="silver" width={16} />
						}{comment.data.gildings.gid_1 > 1 && comment.data.gildings.gid_1}</span>
					</div>
					<ReactQuill style={{ display: 'inline-block' }} onKeyDown={this.handlePress} theme={null} value={comment.data.body_html} onChange={this.propagateHtmlChange.bind(this)} />
					{Array.isArray(comment.data.replies) && comment.data.replies.map(d => <Comment key={d.data.author} comment={d} onChange={onChange} />)}
				</div>
			</div>
		)
	}
}

export default Comment;