import React from 'react';
import { getThread, sendToRender } from './api';
import './App.css';
import PickComments from './PickComments/index';
import SelectThread from './SelectThread';
import Settings from './Settings';

class App extends React.Component {
	constructor(props) {
		super(props)
		this.state = { stage: 0, comments: [], question: null, processed: null, options: { song: null } }
	}
	fetchThread(thread) {
		getThread(thread)
			.then(json => {
				this.setState({
					question: json[0],
					comments: json[1],
					stage: 1,
				})
			})
	}
	commentsDone(commentData) {
		console.log(commentData)
		this.setState({ processed: commentData, stage: 2 })
	}
	addSettings(options) {
		console.log(options)
		this.setState({ options, stage: 3 })

		sendToRender(this.state.question, this.state.processed, options)
	}
	render() {
		if (this.state.stage === 0) {
			return (
				<div className="App">
					<SelectThread onSelection={this.fetchThread.bind(this)} />
				</div>
			)
		} else if (this.state.stage === 1) {
			return (
				<div className="App">
					<PickComments onSelection={this.commentsDone.bind(this)} comments={this.state.comments} />
				</div>
			)
		} else if (this.state.stage === 2) {
			return (
				<div className="App">
					<Settings onSelection={this.addSettings.bind(this)} />
				</div>
			)
		} else if (this.state.stage === 3) {
			return (
				<div className="App">
					<h1>rendering atm bro</h1>
					<p>You may close this tab</p>
				</div>
			)
		}
	}
}

export default App;