import React from 'react'

class Thread extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			input: ''
		}
	}
	onSubmit(e) {
		e.preventDefault()

		let thread = this.state.input

		if (thread.indexOf('comments') !== -1) {
			let pattern = /\/comments\/([\d\w]+)/
			let [_, res] = thread.match(pattern)
			thread = res
		}

		this.props.onSelection(thread)
	}
	handle(e) {
		this.setState({ input: e.target.value })
	}
	render() {
		return (
			<div className="Thread">
				<h1>Choose a thread</h1>
				<form onSubmit={this.onSubmit.bind(this)}>
					<input value={this.state.input} onChange={this.handle.bind(this)} placeholder="Put thread ID or url" autoFocus />
					<button>OK</button>
				</form>
			</div>
		)
	}
}

export default Thread;